import {
  getCorporateServiceAccountCredentials,
  getPublicUserAccountCredentials,
} from '../rest/SaaspassExtensionResource';

const getCredentials = async (type, accId, isShared) => {
  const { otp, password } = isShared
    ? await getCorporateServiceAccountCredentials(accId, type)
    : await getPublicUserAccountCredentials(accId, type);
  return otp || password;
};

export default getCredentials;
